* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
}

#root {
   display: flex;
   flex-direction: column;
   align-items: center;
   background: rgb(227, 229, 233);
}

.app {
   width: 800px;
   
}

.post {
   display: flex;
   justify-content: space-between;
   align-items: center;
   padding: 15px;
   border: 2px solid teal;
   margin-top: 15px;
}

.post__btns {
   display: flex;
}

.post-enter {
   opacity: 0;
 }
 .post-enter-active {
   opacity: 1;
   transition: opacity 500ms ease-in;
 }
 .post-exit {
   opacity: 1;
 }
 .post-exit-active {
   opacity: 0;
   transition: opacity 500ms ease-in;
 }

/* стили для пагинации */

.page__wrapper {
   margin-top: 30px;
   display: flex;
}

.page {
   padding: 10px;
   border: 1px solid teal;
   cursor: pointer;

}

.page__current {
   border: 2px solid orange;
   font-weight: bold;
}

.navbar {
   height: 50px;
   width: 100vw;
   display: flex;
   align-items: center;
   padding:0 15px;
   background: rgb(227, 229, 233);

}

.navbar__links {
   margin-left: auto;
   margin-right: 5px;
}